import dataMenu from '../menu.json'
import menuTemlate from '../template/menu.hbs'

const cardMarkUp = (data, template) => {
    return data.map(template).join('')
}
const listMenu = cardMarkUp(dataMenu, menuTemlate)
console.log(listMenu);
const list = document.querySelector('.js-menu')
list.insertAdjacentHTML('beforeend', listMenu)